/* ===== ë°˜ì‘í˜• ===== */
@media (max-width: 768px) {
  .container {
    padding: 14px;
  }

  h2 {
    font-size: 1.5rem;
  }

  input[type="text"],
  textarea,
  select {
    font-size: 0.95rem;
    padding: 12px 14px;
  }

  button[type="submit"] {
    width: 100%;
    padding: 12px;
    font-size: 1rem;
    text-align: center;
  }
}

@media (max-width: 480px) {
  .container {
    padding: 10px;
  }

  h2 {
    font-size: 1.3rem;
  }

  input[type="text"],
  textarea,
  select {
    font-size: 0.9rem;
    padding: 10px 12px;
  }

  button[type="submit"] {
    font-size: 0.95rem;
    padding: 10px;
    text-align: center;
  }
}
