  #terms-modal {
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    background-color: rgba(0,0,0,0.4); /* ë°˜íˆ¬ëª… ë°°ê²½ */
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 9998;
    padding: 20px;
  }

  .modal-content {
    background-color: #fff;
    padding: 30px;
    max-width: 700px;
    width: 90%;
    border-radius: 12px;
    box-shadow: 0 4px 20px rgba(0, 0, 0, 0.15);
    color: #333;
    max-height: 80vh;
    overflow: hidden;
    display: flex;
    flex-direction: column;
  }

  .modal-content h2 {
    margin-top: 0;
    font-size: 1.6em;
    color: #051D42;
    border-bottom: 2px solid #eee;
    padding-bottom: 10px;
  }

  .terms-scroll {
    overflow-y: auto;
    margin-top: 20px;
    padding-right: 10px;
  }
    .terms-scroll::-webkit-scrollbar {
    width: 10px; /* ìŠ¤í¬ë¡¤ë°” ë„ˆë¹„ */
    }

    .terms-scroll::-webkit-scrollbar-track {
    background: #f1f1f1; /* íŠ¸ë™ ë°°ê²½ */
    border-radius: 10px;
    }

    .terms-scroll::-webkit-scrollbar-thumb {
    background-color: #051D42; /* ìŠ¤í¬ë¡¤ë°” ìƒ‰ìƒ */
    border-radius: 10px;
    border: 2px solid #f1f1f1; /* thumb ì£¼ë³€ ì—¬ë°± íš¨ê³¼ */
    }

    .terms-scroll::-webkit-scrollbar-thumb:hover {
    background-color: #3e4a61; /* hover ì‹œ ìƒ‰ìƒ */
    }

  .terms-scroll p {
    line-height: 1.7;
    font-size: 0.95em;
  }

  .terms-scroll strong {
    color: #051D42;
  }

  /* ëª¨ë°”ì¼ ëŒ€ì‘ */
  @media (max-width: 600px) {
    .modal-content {
      padding: 20px;
      max-height: 85vh;
    }

    .modal-content h2 {
      font-size: 1.3em;
    }
  }
